import md5 from '@/md5.js'
import {
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			username: '',
			password: '',
			utoken: ''
		};
	},
	methods: {
		...mapMutations(['login']),
		getapi() {
			var timestamp = Date.parse(new Date());
			var api_key = "abcde";
			return [timestamp, md5(api_key + timestamp)]; //返回api_key+时间戳md5加密 
		},
		async userlogin() {
			var key = await
			this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/account/login',
				method: 'POST',
				data: {
					time: key[0],
					token: key[1],
					username: this.username,
					password: this.password
				},
				success: (res) => {
					console.log(res.data)
					if (res.data.success === 0) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						})
						this.password = ''
					} else {
						this.login(res.data.userInfo);
						this.utoken = res.data.userInfo.utoken;
						uni.showToast({
							title: "登录成功",
							duration: 2000
						})
						uni.reLaunch({
							url: '../discover/discover'
						});
					}
				}
			});
		},
		gotoregistered(){
			uni.navigateTo({
				url: '../registered/registered'
			})
		},
		wxGetUserInfo(res){
			uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						console.log('-------获取code-------')
						console.log(loginRes.code);
						wx.request({
							url: 'https://xxxxx'+loginRes.code,
							success: function(info) {
								console.log('-------获取sessionKey、openid(unionid)-------')
								console.log(info);
							},
							fail: function(e) {
								console.log(e)
							}
						})
					}
				});
		}
	}
}
